export default {
  data () {
    return {
      avatarUrl: '',
      menuList: [{
        title: '用户昵称',
        name: 'nickName',
        content: ''
      }, {
        title: '性别',
        name: 'gender',
        content: ''
      }, {
        title: '个性签名',
        name: 'sign',
        content: ''
      }, {
        title: '绑定手机',
        name: 'mobile',
        content: ''
      }]
    }
  },
  onLoad() {
    let that= this
    that.getUserInfo()
  },
  methods:{
    getUserInfo() {
      let that= this,
        params= {
          url: that.$api.getUserInfo
        }
      that.$httpRequest(params).then((res) => {
        console.log(res.data)
        let info= res.data.info,
          list= that.menuList
        list.map((obj, index) => {
          console.log(obj.name, info[obj.name], 'obj')
          obj.content= info[obj.name]
        })
        that.avatarUrl= info.avatarUrl
        that.menuList= list
      })
    }
  }
}
